/*Font CSS*/
    @import url('https://fonts.googleapis.com/css?family=Libre+Baskerville:400,400i');

    @font-face {
        font-family: GilroyExtraBold;
        src: url("./Gilroy-ExtraBold.otf") format("opentype");
    }

    @font-face {
        font-family: GilroyLight;
        src: url("./Gilroy-Light.otf") format("opentype");
    }
/*End Font Css*/

body, html {
  margin: 0;
  padding: 0;
  border: 0;
  text-align: center;
  height: 100%;
  width: 100%;
}


#header {
  background-image: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(0,0,0,0.75) 100%), url("./resources/Header_BG.jpg");
  background-size: cover;
  background-position: center;
  height: 100vh;
  width: 100vw;
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}

#title {
  display: inline;
}

.content {
  width: 75%;
  margin: 0 auto;
  height: auto;
  padding: 9% 9%;
}

.design {
  padding: 0 0;
  height: auto;
  margin: 0 auto;
  width: 75%;
}

.designContainer {
    margin-left: auto;
    margin-right: auto;
}

.bigImg {
  width: 100%;
  height: 40em;
}

.smallImg {
  padding-left: 15px;
  padding-right: 15px;
  margin-right: auto;
  margin-left: auto;
}

/* images */
  .bgImg {
    background-position: center;
    background-size: cover;
  }

  .veru_img01 {
    background-image: url("./resources/Veru_Logo_01.jpg");
  }
  .veru_img02 {
    background-image: url("./resources/Veru_Perspective_Web.jpg");
  }

  .designImg {
    max-width: 100%;
    height: auto;
    padding-bottom: 3%;
  }

  .personasImg {
    padding: 5% 0%;
  }

/* end images */

/* Colors */
  .white {
    color: #FFFFFF;
  }

  .red {
    color: #ff725c;
  }
/* End Colors */

/* Text Styles */
  .titleFont {
    font-weight: 400;
    font-style: italic;
    font-family: "Libre Baskerville", serif;
    font-size: 5em;
  }

  .normalText {
    font-family: "GilroyLight";
    font-size: 1rem;
  }

  .contentText {
    text-align: left;
    font-size: 1.3rem;
  }

  .contentTitle {
    font-size: 4rem;
  }

  .innerTittle {
    font-size: 2.5rem;
  }
/* End Text Styles */

@media (min-width: 768px) {
  .designContainer {
      width: 750px;
  }
}

@media (min-width: 992px) {
  .designContainer {
      width: 970px;
  }
}

@media (min-width: 1200px) {
  .designContainer {
    width: 1170px;
  }
}